.navbar {
  padding-left: 3rem;
  padding-right: 3rem;
}

#hero {
  padding-top: 10rem;
  padding-bottom: 11rem;
}

.hero_cta {
  padding-top: 2rem;
}

.schedule_img {
  height: 25rem;
  width: 25rem;
}
@media (max-width: 767px) {
  .schedule_img {
    padding-top: 7rem;
  }
}

#explanation {
  background-color: #f2f2f2;
  padding: 50px;
  text-align: center;
}

.explanation_item {
  font-size: 1.25rem;
}

.explanation_img {
  height: 50px;
  margin-bottom: 20px;
  // This filter works by changing the color assuming the original img are black svg.
  // Color picker for this: https://codepen.io/sosuke/pen/Pjoqqp
  filter: invert(62%) sepia(9%) saturate(2194%) hue-rotate(71deg)
    brightness(97%) contrast(102%);
}

#features {
  padding-top: 5rem;
  font-size: 17px;
}

.feature_row {
  padding-top: 5rem;
}

.feature_header {
  font-size: 1.55rem;
}

.feature_explanation {
  font-size: 1.15rem;
}

.features_img {
  height: 16rem;
}
@media (max-width: 767px) {
  .features_img {
    padding-bottom: 2rem;
  }
}

#use_cases {
  background-color: #f2f2f2;
  margin-top: 5rem;
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.use_case_item {
  padding-top: 2rem;
}

.use_case_header {
  font-size: 1.55rem;
}

.use_case_explanation {
  font-size: 1.15rem;
}

/**
// This one adds a background with gradient to the pricing section
section.pricing {
  background: #007bff;
  background: linear-gradient(to right, #0062e6, #33aeff);
}
*/
section.pricing {
  margin-bottom: 2rem;
}

.pricing .card {
  border: none;
  border-radius: 1rem;
  transition: all 0.2s;
  box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.5);
}

.pricing hr {
  margin: 1.5rem 0;
}

.pricing .card-title {
  margin: 0.5rem 0;
  font-size: 0.9rem;
  letter-spacing: 0.1rem;
  font-weight: bold;
}

.pricing .card-price {
  font-size: 3rem;
  margin: 0;
}

.pricing .card-price .period {
  font-size: 0.8rem;
}

.pricing ul li {
  margin-bottom: 1rem;
}

.pricing .text-muted {
  opacity: 0.7;
}

.pricing .btn {
  font-size: 80%;
  border-radius: 5rem;
  letter-spacing: 0.1rem;
  font-weight: bold;
  padding: 1rem;
  opacity: 0.7;
  transition: all 0.2s;
}

/* Hover Effects on Card */
@media (min-width: 992px) {
  .pricing .card:hover {
    margin-top: -0.25rem;
    margin-bottom: 0.25rem;
    box-shadow: 0 0.5rem 1rem 0 rgba(0, 0, 0, 0.8);
  }
  .pricing .card:hover .btn {
    opacity: 1;
  }
}
